/**
 * 1.dataurlbaseUrl
 * 2.datadataparams
 */
export default function normalizeConfig(config) {
  const data = config.data
  if(data) {
    config.url = injectDataIntoURL(config.url, data)
    config.baseURL = injectDataIntoURL(config.baseURL, data)

    // allow to use data intead of params
    if (noRequsetBody(config.method)) {
      config.params = {
        ...config.params,
        ...data
      }
      delete config.data
    }
  }
  return config
}

/**
 * @param {string} url /videos/v:vedio_id
 * @param {object} data url
 */
function injectDataIntoURL(url, data) {
  if(!url) return url
  const temp = url.split('?')
  temp[0] = temp[0].replace(/:([A-Za-z$_][A-Za-z0-9$_]*)/g, (str, key) => {
    const value = data[key]
    delete data[key]
    return value
  })
  if(temp[1]) {
    return temp.join('?')
  } else {
    return temp[0]
  }
}

/**
 * `data`  'PUT', 'POST',  'PATCH'
 */
function noRequsetBody(method) {
  return !/^(put|post|patch)$/i.test(method)
}
