import mergeConfig from './merge-config'
import normalizeConfig from './normalize-config'
import handleError from './handle-error'
import request from './request'
import { Cancel } from 'axios'

/**
 * Requester
 * 1.{object} config.errorHandler 
 * 2.{array} config.before & config.after
 * 3.{Brake} config.cancelTokenconfig.brakeSPABrakeRequesterBrake
 * 4.{Manager} config.manager 
 */
export default class Requester {
  /**
   * uniquemanagerApi
   *
   * @param {object} [config.errorHandler] 
   * @param {function(responseData): (number|string)} [config.errorHandler.getCodeFromData] 
   * @param {function(responseData): string} [config.errorHandler.getMessageFromData] 
   * @param {function(errorCode): string} [config.errorHandler.getMessageFromCode] code
   * @param {function(error)} [config.errorHandler.globalHandler] 
   * @param {object.<errorCode, callback: (function(error, requestFn, next): (Promise|undefined))>} [config.errorHandler.interceptorMapping] nextcatch
   *
   * @param {object|boolean} [config.unique] true{ strict: false, action: 'prevent' }
   * @param {boolean} [config.unique.strict=false] falseapitrueurl
   * @param {string} [config.unique.action=prevent] [prevent|override]'prevent''override'
   *
   * @param {RequesterManager} [config.manager]
   */
  constructor(config, api) {
    this.config = config
    this.api = api
    this.done = false
    this.aborted = false
    this.promise = new Promise(resolve => {
      this.resolvePromise = resolve
    })
    if(this.config.brake) {
      this.config.brake.push(this.abort.bind(this))
    }
  }
  clone() {
    return new Requester(mergeConfig(this.config), this.api)
  }
  abort(message) {
    if(!this.aborted) {
      this.aborted = true
      this.abortReason = new Cancel(message)
      if(!this.done) {
        this.resolvePromise(Promise.reject(this.abortReason))
      } else {
        this.abortRequest && this.abortRequest(message)
      }
    }
  }
  send(data = {}) {
    if(!this.done && !this.aborted) {
      this.done = true
      const config = normalizeConfig(mergeConfig(this.config, { data }))
      const {
        manager,
        before,
        after,
      } = config

      let prelude = Promise.resolve(config)
      if(before) {
        prelude = prelude.then(before).then(newConfig => newConfig || config)
        // prelude = prelude.then(config => before(config) || config)
      }
      if(manager) {
        prelude = prelude.then(config => Promise.resolve(manager.register(this)).then(() => config))
      }

      const execute = () => {
        let result = prelude.then(dispatchRequest.bind(this))
        if(after) {
          result = result.then(after)
        }
        return result.catch(err => handleError(err, execute, this))
      }

      this.resolvePromise(execute())
    }
    return this.promise
  }
}

function dispatchRequest(config) {
  if(this.abortReason) {
    throw this.abortReason
  }
  const {
    value,
    abort
  } = request(config)
  this.abortRequest = abort
  return value
}
